import React from "react"
import { Divider, Button, Icon } from "antd"
import Slider from "react-slick"
import { projects } from "../utils/projects"
var settings = {
  dots: true,
  arrow: true,
  infinite: true,
  speed: 500,
  slidesToShow: 4,
  slidesToScroll: 4,
  initialSlide: 0,
  responsive: [
    {
      breakpoint: 1024,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 3,
        infinite: true,
        dots: true,
      },
    },
    {
      breakpoint: 600,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
        initialSlide: 2,
      },
    },
    {
      breakpoint: 480,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
      },
    },
  ],
}

const Card = props => {
  const { title, tags, caption, viewLink, gitLink } = props
  return (
    <div className="card">
      <h1 className="card-title">{title}</h1>
      <p className="card-tags">{tags}</p>
      <p className="card-caption">{caption}</p>
      <center>
        <div className="btn-group">
          <a href={viewLink}>
            <Button type="primary" style={{ marginRight: 6 }} shape="round">
              View
            </Button>
          </a>
          <a href={gitLink}>
            <Button type="normal" color="black" shape="round">
              Open on Github{" "}
              <Icon style={{ color: "#333", fontSize: 15 }} type="github" />
            </Button>
          </a>
        </div>
      </center>
    </div>
  )
}

const Projects = () => {
  return (
    <div className="skills" id="projects">
      <h1 className="title">Projects</h1>
      <p className="desc">
        I love to create awesome stuff by myself as well as by contributing to
        open source. I contribute to <a href="https://fossasia.org">FOSSASIA</a>{" "}
        and other open source projects to bring ease to lives of people. Here
        are some of the projects I have created:
      </p>
      <Slider {...settings}>
        {projects.map(e => (
          <Card
            title={e.title}
            tags={e.tags}
            caption={e.caption}
            viewLink={e.viewLink}
            gitLink={e.gitLink}
          />
        ))}
      </Slider>
      <Divider />
    </div>
  )
}

export default Projects
